import { query } from "./strapi";

export function getHomeInfo() {
  const { STRAPI_HOST } = process.env;
  return query("pagina-principal")
    .then(res => {
      const { tituloMultimedia, descripcion, encabezado_1, encabezado_2, encabezado_3, videoCover } = res.data;
      const video = `${STRAPI_HOST}${videoCover[0].url}`
      
      return { tituloMultimedia, descripcion, encabezado_1, encabezado_2, encabezado_3, video };
    })
}