// app/page.tsx (o donde se encuentra tu archivo Home)
import { Header } from '@/components/Header';
import { Hero } from '@/components/Hero';
import { getHomeInfo } from '@/lib/get_home_info';

// Tipos de los datos que vas a recibir
interface HomeInfo {
  encabezado_1: string;
  encabezado_2: string;
  encabezado_3: string;
  descripcion: string;
  tituloMultimedia: string;
  video: string;
}

// Función asíncrona para obtener los datos antes de renderizar la página
const fetchHomeInfo = async (): Promise<HomeInfo> => {
  try {
    const homeInfo = await getHomeInfo(); // Obtener los datos desde tu API o base de datos
    // Verificar si homeInfo es válido
    if (!homeInfo) {
      throw new Error('No se pudo obtener la información.');
    }
    return homeInfo;
  } catch (error) {
    console.error('Error al cargar la información del home:', error);
    return {
      encabezado_1: '',
      encabezado_2: '',
      encabezado_3: '',
      descripcion: '',
      tituloMultimedia: '',
      video: '',
    }; // Devuelve un objeto vacío en caso de error
  }
};

export default async function Home() {
  const homeInfo = await fetchHomeInfo(); // Obtener los datos antes de renderizar

  return (
    <main>
      <Header />
      <div className="max-w-[1400px] mx-auto">
        <Hero homeInfo={homeInfo} /> {/* Pasar los datos a Hero */}
      </div>
    </main>
  );
}
